import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: false,
			search:'',
			list:[],
			picArr:[],
            sState:0,
            SubjectBuyRecordId:0,
        }
    },
    created(){
		this.$bridge.callhandler('getSubjectBuyRecordId','',(data) => {
            this.SubjectBuyRecordId = data
            this.getLookMDTMedical(data)
            this.isMDTMedical(data)
		    responseCallback('接收到方法')
        })
        // this.getLookMDTMedical(634)
        // this.isMDTMedical(634)
    },
    mounted(){
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        isbrowser(){
            const browser = navigator.userAgent
            if(browser.indexOf('iPhone') !== -1){
                this.$router.push('/mdt/picAndroid?SubjectBuyRecordId='+this.SubjectBuyRecordId)  //ios
            }else{
                this.$router.push('/mdt/picAndroid?SubjectBuyRecordId='+this.SubjectBuyRecordId)
            }
        },
		getLookMDTMedical(data){
			this.$get('MDTAPI/MDTMedical/QueryLookMDTMedical',{
				SubjectBuyRecordId:data
			}).then(o => {
				this.list = o
                for(var i = 0; i<this.list.length;i++){
                    if(this.list[i].ImagePaths && this.list[i].ImagePaths.length){
                        for(var y = 0; y<this.list[i].ImagePaths.length;y++){
                            this.picArr.push(this.list[i].ImagePaths[y])
                        }
                    }
                }
                if(!this.picArr.length){
                    this.haveMore = true
                }
			})
		},
        isMDTMedical(data){
        	this.$get('MDTAPI/MDT/QueryMDTBySubjectBuyRecordId',{
        		SubjectBuyRecordId:data
        	}).then(o => {
                this.sState = o.SubjectStatus
                console.log(o.SubjectStatus)
        	})
        },
		picChoose(){
            if(!this.picArr.length){
                this.$toast("暂无图片资料,请去补充")
                return
            }
			this.$store.dispatch('pushpicArr',this.picArr)
			this.$router.push('/mdt/picChoose')
		},
		bigImage(o,num){
		    const data = {
		        position: num,
		        url: [o.ImagePath]
		    }
            console.log(data)
		    this.$bridge.callhandler('Preview', JSON.stringify(data))
		},
        addMdtDocuments(){
            this.$bridge.callhandler('addMdtDocuments', '')
        }

    }
}



